.todo-list-item {
    font-size: 1.25rem;
  }
  
  .todo-list-item button {
    width: 35px;
    margin: 3px;
  }
  
  .todo-list-item-label {
    line-height: 35px;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
  }
  
  .todo-list-item.done .todo-list-item-label {
    text-decoration: line-through;
  }
  
  .todo-list-item.important .todo-list-item-label {
    font-weight: bold;
    color: steelblue;
  }
  
.todo-list .list-group-item {
    padding: .25rem .75rem
}
.app-header {
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.app-header h1 {
  text-transform: uppercase;
  color:#324d79;
  font-family: 'Raleway', sans-serif;
  font-weight: 800;
  font-style:italic;
}
.app-header h2 {
  font-size: 1.2rem;
  color: #1a4142;
}
.search-input {
    width: auto;
    flex-grow: 1;
    margin-right: 3px;
}
.todo-list-item-label {
    color:#283a4e;
    font-family: 'Raleway', sans-serif;
    font-weight: bold;
    overflow: hidden;
    margin-right:3px;
}
@media(max-width:545px) {
    .top-panel {
        flex-direction: column-reverse;
    }
    .search-input {
        margin-right:0;
    }
    .todo-list-item-label {
        margin-left:0;
        font-size: .9rem;
    }
    .btn-group {
        padding-bottom: 3px;
    }
}
@media(max-width:400px) {
    .todo-list-item-label {
        font-size: .7rem;
    }
}


.item-add-form {
    margin-top: 1rem;
}
.item-add-form input {
    margin-right:3px;
}
.btn-outline-secondary {
    color:#093052;
    border-color:#81b6e4;
} 
.btn-outline-secondary:hover {
    background-color: #17a2b8;
    border-color: #17a2b8;
}
.btn-outline-secondary:not(:disabled):not(.disabled):active, .show>.btn-outline-secondary.dropdown-toggle {
    color: #fff;
    background-color: transparent; 
    border-color: #10707f;
}
.app-container {
    min-height:100vh;
    background:linear-gradient(to right, rgba(147,206,222,1) 0%, rgba(117,189,209,1) 41%, rgba(77,135,151,1) 70%, rgba(52,101,115,1) 88%);;
}
.todo-app {
    padding:2rem 0;
    max-width:70%;
    background: transparent;
    position: relative;
    z-index:10;
}
.top-panel {
    margin: 1rem 0;
}

/*----------------------------------------------------------------------------------*/
/*------------------------------RESET DEFAULT STYLES--------------------------------*/
/*----------------------------------------------------------------------------------*/
html,
body {
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  overflow-x: hidden; }

body {
  font: 12px Tahoma, sans-serif;
  background: #fff;
  color: #000; }

div,
menu,
p,
a,
span,
em,
strong,
img,
h1,
h2,
h3,
h4,
h5,
h6,
ul,
ol,
li,
dl,
dt,
dd,
table,
td,
tr,
form,
fieldset,
legend,
label,
select,
input,
textarea,
button,
figure {
  padding: 0;
  margin: 0;
  outline: none;
  list-style: none; }

button {
  cursor: pointer;
  padding: 0; }

textarea {
  vertical-align: top; }

input[type=submit] {
  cursor: pointer; }

img,
fieldset {
  border: 0; }

img {
  vertical-align: top; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

button:focus {
  outline: none; }
  /*----------------------------------------------------------------------------------*/
/*-------------------------------------DEFAULT CLASSES------------------------------*/
/*----------------------------------------------------------------------------------*/
* {
    box-sizing: border-box; }
  
  a {
    text-decoration: none;
    color: #fff; }
  
  a:focus, a:hover {
    color: #2098D1;
    text-decoration: none; }
  
  ol {
    list-style-type: decimal; }
  
  img {
    width: 100%;
    display: block;
    height: auto; }
/*-------------------------------------------*/
/*-------------------------------------------*/
/*-------------------------------------------*/
.particles {
    position: fixed;
    top:0;
    bottom:0;
    right:0;
    left:0;
}
